import React, { useState , useEffect } from 'react';
import SearchBox from './Components/SearchBox';
import Checker from './Containers/Checker';

function App() {
  const api ={
    base: "https://api.openweathermap.org/data/2.5/",
    key: "b081d845d97c203d7c96e5cfd9b8fe23"
  }

  const [query , setQuery] = useState("");
  const [weather , setWeather] = useState({});

  const onSearchChange = (e) => setQuery(e.target.value)

  const onKeyPress = (evt) => {
    if (evt.key === "Enter") {
      fetch(`${api.base}weather?q=${query}&units=metric&APPID=${api.key}`)
        .then(res => res.json())
        .then(result => {
          setWeather(result);
          setQuery('');
          console.log(result);
        });
    }
  }

  return (
    <div className={(typeof weather.main != "undefined") ? ((weather.main.temp > 16) ? 'app warm' : 'app') : 'app'}>
      <main>
        <SearchBox onSearchChange={onSearchChange} value={query} onKeyPress={onKeyPress} />
        <Checker weather={weather} />
      </main>
    </div>
  );
}

export default App;
